"use client";

import { useEffect, useRef, useState } from "react";
import { MessageType, RequestType } from "@/interface";

export default function Page() {
  const localRef = useRef<HTMLVideoElement | null>(null);
  const remoteRef = useRef<HTMLVideoElement | null>(null);
  const [pc, setPc] = useState<RTCPeerConnection | null>(null);
  const [socket, setSocket] = useState<WebSocket | null>(null);

  useEffect(() => {
    const configuration = {
      iceServers: [{ urls: "stun:stun.l.google.com:19302" }],
    };

    const peer = new RTCPeerConnection(configuration);
    const ws = new WebSocket("ws://localhost:8080");

    setPc(peer);
    setSocket(ws);

    // ðŸ”¹ Attach local stream
    getStream(peer);

    // ðŸ”¹ Handle remote tracks
    peer.ontrack = (event) => {
      if (remoteRef.current) {
        let stream = remoteRef.current.srcObject as MediaStream;
        if (!stream) {
          stream = new MediaStream();
          remoteRef.current.srcObject = stream;
        }
        if (!stream.getTracks().find((t) => t.id === event.track.id)) {
          stream.addTrack(event.track);
        }
      }
    };

    // ðŸ”¹ ICE candidates
    peer.onicecandidate = (e) => {
      if (e.candidate) {
        ws.send(
          JSON.stringify({
            type: RequestType.ADD_ICE_CANDIDATES,
            payload: { sdp: e.candidate },
          })
        );
        console.log("Sent ICE");
      }
    };

    // ðŸ”¹ WebSocket listener
    ws.onmessage = async (e) => {
      const data = JSON.parse(e.data.toString());
      const { type, payload } = data;

      if (!type || !payload) return;

      if (type === RequestType.OFFER) {
        // Remote sent offer â†’ set remote desc + send answer
        await peer.setRemoteDescription(new RTCSessionDescription(payload.offer));
        const answer = await peer.createAnswer();
        await peer.setLocalDescription(answer);

        ws.send(
          JSON.stringify({ type: RequestType.ANSWER, payload: { answer } })
        );
        console.log("Answer sent");
      } else if (type === RequestType.ANSWER) {
        // Remote answered â†’ set remote desc
        await peer.setRemoteDescription(new RTCSessionDescription(payload.answer));
        console.log("Answer received");
      } else if (type === RequestType.ADD_ICE_CANDIDATES) {
        const c = payload.sdp;
        if (c) {
          try {
            await peer.addIceCandidate(new RTCIceCandidate(c));
            console.log("ICE candidate added");
          } catch (err) {
            console.warn("Error adding ICE", err);
          }
        }
      } else if (type === MessageType.MATCHED) {
        console.log("Matched!");
        sendOffer(peer, ws);
      }
    };
  }, []);

  async function sendOffer(peer: RTCPeerConnection, ws: WebSocket) {
    const offer = await peer.createOffer();
    await peer.setLocalDescription(offer);
    ws.send(JSON.stringify({ type: RequestType.OFFER, payload: { offer } }));
    console.log("Offer sent");
  }

  async function getStream(peer: RTCPeerConnection) {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true,
    });
    stream.getTracks().forEach((track) => peer.addTrack(track, stream));

    if (localRef.current) {
      localRef.current.srcObject = stream;
      await localRef.current.play();
    }
  }

  return (
    <div className="flex justify-center items-center h-screen w-screen gap-4 bg-black">
      <div>
        <video
          ref={localRef}
          autoPlay
          muted
          playsInline
          className="w-80 h-60 bg-black"
        />
        <p className="text-center mt-1 text-white">Local</p>
      </div>
      <div>
        <video
          ref={remoteRef}
          autoPlay
          playsInline
          className="w-80 h-60 bg-black"
        />
        <p className="text-center mt-1 text-white">Remote</p>
      </div>
    </div>
  );
}
